
.vutton-logo {
  text-align: center;
}
.vutton {
  margin: 0 auto;
}
.vutton__text {
  font-size: 22px;
  letter-spacing: 0.2rem;
}


.vutton {
  position: relative;
  width: 200px;
  height: 60px;
  cursor: pointer;
}
.vutton__border {
  fill: transparent;
  stroke-dasharray: 520;
  stroke-dashoffset: 0;
  stroke-width: 4px;
  stroke: #42b983;
}
.vutton__text {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  color: #34495e;
  line-height: 60px;
}
@-webkit-keyframes vutton__ani {
0% {
    stroke-dasharray: 200 400;
    stroke-dashoffset: 0;
    stroke-width: 4px;
    stroke: #42b983;
}
100% {
    stroke-dasharray: 200 320;
    stroke-dashoffset: -520;
    stroke-width: 4px;
    stroke: #42b983;
}
}
@keyframes vutton__ani {
0% {
    stroke-dasharray: 200 400;
    stroke-dashoffset: 0;
    stroke-width: 4px;
    stroke: #42b983;
}
100% {
    stroke-dasharray: 200 320;
    stroke-dashoffset: -520;
    stroke-width: 4px;
    stroke: #42b983;
}
}
.vutton:hover .vutton__border {
  -webkit-animation: 3s vutton__ani linear forwards infinite;
  animation: 3s vutton__ani linear forwards infinite;
}
